
'use client';

import * as React from 'react';
import { motion } from 'framer-motion';
import { Card, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Bell, Map, ShieldCheck, Ticket, Lightbulb, ArrowRight } from 'lucide-react';
import { cn } from '@/lib/utils';

const features = [
  {
    icon: <Map className="h-8 w-8" />,
    title: 'Real-Time Bus Tracking',
    description: 'See exactly where your bus is on a live map, with accurate ETA predictions to your stop.',
    color: 'from-blue-500 to-cyan-500',
    stats: '99.9% Accuracy',
  },
  {
    icon: <Ticket className="h-8 w-8" />,
    title: 'Seamless Seat Booking',
    description: 'Book your seat in advance with a visual seat map and download your digital ticket instantly.',
    color: 'from-purple-500 to-pink-500',
    stats: '3-Click Booking',
  },
  {
    icon: <Bell className="h-8 w-8" />,
    title: 'Instant Notifications',
    description: 'Receive timely alerts for booking confirmations, departure times, and potential delays.',
    color: 'from-green-500 to-emerald-500',
    stats: 'Real-time Alerts',
  },
  {
    icon: <Lightbulb className="h-8 w-8" />,
    title: 'AI-Powered Route Insights',
    description: 'Get smart summaries of routes, including key stops and points of interest, to plan your travel better.',
    color: 'from-orange-500 to-red-500',
    stats: 'Smart Analytics',
  },
  {
    icon: <ShieldCheck className="h-8 w-8" />,
    title: 'Safe and Secure',
    description: 'Built with role-based access and security-first principles to protect your data.',
    color: 'from-indigo-500 to-purple-500',
    stats: 'Bank-level Security',
  },
];

const containerVariants = {
  hidden: { opacity: 0 },
  visible: {
    opacity: 1,
    transition: {
      staggerChildren: 0.1,
    },
  },
};

const itemVariants = {
  hidden: { opacity: 0, y: 20 },
  visible: {
    opacity: 1,
    y: 0,
    transition: {
      type: 'spring',
      damping: 20,
      stiffness: 100,
    },
  },
};

const FeatureCard = ({ feature, index }: { feature: (typeof features)[0], index: number }) => (
  <motion.div 
    className="mx-4 flex-shrink-0" 
    style={{ width: 'clamp(20rem, 30vw, 24rem)'}}
    variants={itemVariants}
  >
    <div className="group relative h-full overflow-hidden rounded-2xl bg-card border border-border/50 shadow-lg transition-all duration-500 hover:shadow-2xl hover:scale-105">
      {/* Gradient Background */}
      <div className={`absolute inset-0 bg-gradient-to-br ${feature.color} opacity-0 group-hover:opacity-10 transition-opacity duration-500`} />
      
      {/* Icon Background */}
      <div className="absolute top-6 right-6 opacity-5 group-hover:opacity-10 transition-opacity duration-500">
        <div className={`text-6xl bg-gradient-to-br ${feature.color} bg-clip-text text-transparent`}>
          {feature.icon}
        </div>
      </div>

      <div className="relative z-10 p-8 h-full flex flex-col">
        {/* Stats Badge */}
        <div className="mb-4">
          <span className="inline-flex items-center px-3 py-1 rounded-full text-xs font-medium bg-primary/10 text-primary">
            {feature.stats}
          </span>
        </div>

        {/* Icon */}
        <div className="mb-6">
          <div className={`inline-flex h-16 w-16 items-center justify-center rounded-2xl bg-gradient-to-br ${feature.color} text-white shadow-lg group-hover:scale-110 transition-transform duration-300`}>
            <div className="animate-icon-pulse" style={{ animationDelay: `${index * 250}ms` }}>
              {feature.icon}
            </div>
          </div>
        </div>

        {/* Content */}
        <div className="flex-1">
          <h3 className="font-headline text-xl font-bold text-card-foreground mb-3 group-hover:text-primary transition-colors duration-300">
            {feature.title}
          </h3>
          <p className="text-muted-foreground leading-relaxed mb-4">
            {feature.description}
          </p>
        </div>

        {/* Learn More Link */}
        <div className="pt-4">
          <button className="inline-flex items-center text-sm font-medium text-primary hover:text-primary/80 transition-colors duration-200 group/link">
            Learn more 
            <ArrowRight className="ml-1 h-4 w-4 transition-transform duration-200 group-hover/link:translate-x-1" />
          </button>
        </div>
      </div>
    </div>
  </motion.div>
);

export function Features() {
  return (
    <section id="features" className="relative py-24 sm:py-32 overflow-hidden">
      {/* Background Pattern */}
      <div className="absolute inset-0 opacity-5">
        <div className="absolute inset-0" style={{
          backgroundImage: `radial-gradient(circle at 25% 25%, hsl(var(--primary)) 0%, transparent 50%), 
                           radial-gradient(circle at 75% 75%, hsl(var(--accent)) 0%, transparent 50%)`
        }} />
      </div>

      <div className="container relative max-w-7xl px-4">
        {/* Header */}
        <motion.div 
          className="text-center mb-20"
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.6 }}
        >
          <motion.div
            initial={{ opacity: 0, scale: 0.9 }}
            whileInView={{ opacity: 1, scale: 1 }}
            viewport={{ once: true }}
            transition={{ duration: 0.6, delay: 0.1 }}
          >
            <span className="inline-flex items-center px-4 py-2 rounded-full text-sm font-medium bg-primary/10 text-primary mb-6">
              üöÄ Powerful Features
            </span>
          </motion.div>
          
          <h2 className="font-headline text-4xl md:text-5xl lg:text-6xl font-bold tracking-tighter mb-6">
            Everything You Need for a{' '}
            <span className="gradient-text">Smooth Journey</span>
          </h2>
          
          <p className="mx-auto max-w-3xl text-lg md:text-xl text-muted-foreground leading-relaxed">
            From live tracking to easy booking, our platform simplifies campus transportation 
            with cutting-edge technology and user-centric design.
          </p>
        </motion.div>

        {/* Features Grid - Desktop */}
        <div className="hidden lg:block">
          <motion.div
            className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8"
            variants={containerVariants}
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true }}
          >
            {features.map((feature, index) => (
              <motion.div key={feature.title} variants={itemVariants}>
                <FeatureCard feature={feature} index={index} />
              </motion.div>
            ))}
          </motion.div>
        </div>

        {/* Features Carousel - Mobile/Tablet */}
        <div className="lg:hidden">
          <div
            className="group/container relative w-full overflow-hidden"
            style={{
              maskImage: 'linear-gradient(to right, transparent, black 5%, black 95%, transparent)',
            }}
          >
            <motion.div 
              className="flex w-max items-stretch group-hover/container:[animation-play-state:paused]"
              style={{
                animation: 'scroll 50s linear infinite'
              }}
              initial={{ opacity: 0 }}
              whileInView={{ opacity: 1 }}
              viewport={{ once: true }}
              transition={{ duration: 0.8 }}
            >
              {/* Render cards twice for seamless loop */}
              {features.map((feature, index) => (
                <FeatureCard key={feature.title} feature={feature} index={index} />
              ))}
              {features.map((feature, index) => (
                <FeatureCard key={`${feature.title}-duplicate`} feature={feature} index={index} />
              ))}
            </motion.div>
          </div>
        </div>

        {/* Bottom CTA */}
        <motion.div
          className="text-center mt-16"
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.6, delay: 0.4 }}
        >
          <p className="text-muted-foreground mb-4">Ready to experience the future of campus transport?</p>
          <button className="inline-flex items-center px-6 py-3 rounded-full font-medium gradient-bg-primary text-white hover:scale-105 transition-all duration-300 glow-primary">
            Get Started Today
            <ArrowRight className="ml-2 h-4 w-4" />
          </button>
        </motion.div>
      </div>
    </section>
  );
}
